import Vue from '../../node_modules/vue/dist/vue.common.js';

const app = new Vue({
    el: '#app',
    data:{
        hahow: [],
    },
    ready: function(){
        $.ajax({
            url: "https://awiclass.monoame.com/api/command.php?type=get&name=hahowdata",
            success: function(item){
                app.hahow = JSON.parse(item);
            }
        })
    },
    computed: {
        remainingDays: function(){
            for (var i=0;i<this.hahow.length;i++){
                return Math.floor((new Date(this.hahow[i].proposalDueTime) - new Date(this.hahow[i].incubateTime)) / (1000 * 60 * 60 * 24));
            }
        }
    }
});